
import { _decorator, Component,  Label, Node } from 'cc';
const { ccclass, property } = _decorator;

@ccclass('Score')
export class Score extends Component {
    @property(node)
    scoreNode: Node ;

    score: number = 0;
    onLoad() {
        this.scheduleOnce(() => {
            this.testTime();
        }, 1);

    }

    testTime() {
        this.scheduleOnce(() => {
            this.scoreNode.getComponent(Label).string = this.score + '';

            this.testTime();
        }, 1);
    }
}


